<template>
  <div>
    <sec-input v-model="filterText" placeholder="输入关键字进行过滤"></sec-input>
    <sec-tree
      ref="tree"
      class="mt20"
      default-expand-all
      :data="data"
      :props="defaultProps"
      :filter-node-method="filterNode"
    ></sec-tree>
  </div>
</template>

<script>
import { includes } from 'lodash';

export default {
  data() {
    return {
      filterText: '',
      data: [{
        id: 1,
        label: '一级 1',
        children: [{
          id: 4,
          label: '二级 1-1',
          children: [{
            id: 9,
            label: '三级 1-1-1',
          }, {
            id: 10,
            label: '三级 1-1-2',
          }],
        }],
      }, {
        id: 2,
        label: '一级 2',
        children: [{
          id: 5,
          label: '二级 2-1',
        }, {
          id: 6,
          label: '二级 2-2',
        }],
      }, {
        id: 3,
        label: '一级 3',
        children: [{
          id: 7,
          label: '二级 3-1',
        }, {
          id: 8,
          label: '二级 3-2',
        }],
      }],
      defaultProps: {
        children: 'children',
        label: 'label',
      },
    };
  },
  methods: {
    filterNode(value, data) {
      if (!value) return true;
      return includes(data.label, value);
    },
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    },
  },
};
</script>
